
body,a,p,li,ul,h1,h2,h3,h4,h5,strong,div {
  font:.12rem "Microsoft YaHei","黑体",Helvetica,Arial,Tahoma,sans-serif;
  margin: 0;
  padding: 0;
  border: none;
  text-decoration: none;
  color:#5a5a5a;
}
ul,li {
  list-style: none;
}
#app {
  height: 100%;
}
body {
  height: 100%;overflow-x: hidden !important; max-width: 768px; margin: 0 auto; background-size:100% 100%; background: #fff;
}
html {
  height: 100%;
}
.nav {
  background:#333;
  position: absolute;
  z-index: 99;
  top: 0;
  left: 0;
  width: 100%;
}
.nav img{
  width:.3rem;
  height:.3rem;
  border-radius:50%;
  border:0.01rem solid #fff;
  padding:.08rem;
}
.nav span{
  display: block; margin-top:.05rem;
}
.nav a {
  display: block;
  border-bottom:.01rem solid rgba(255,255,255,0.15);
  padding:.12rem;
  color:#fff;
  text-align: center;
}
.vux-drawer .actives {
  -webkit-transform: translate3d(1.5rem, 0px, 0px) !important;
  transform: translate3d(1.5rem, 0px, 0px) !important;
}
.vux-drawer .activer {
  -webkit-transform: translate3d(-1.5rem, 0px, 0px) !important;
  transform: translate3d(-1.5rem, 0px, 0px) !important;
}
.vux-drawer>.vux-drawer-content {
  background-image:url(./assets/navbar_bg.png); background-color:transparent !important; width:1.5rem; overflow-y: auto !important;
}
.header {
  height: 0.5rem;
  overflow: hidden;
  width: 100%;
  background:rgb(200, 17, 24);
}
.header .logo {
  float: left;
  width: 76%;
  text-align: center;
}
.header .logo p{
  color: #fff;
  height:.5rem;
  line-height:.5rem;
  font-size:.2rem;
}
.header .icon-nav {
  fill: #fff;
  cursor: pointer;
  float: left;
  width:10%;
  text-align: center;
  height:.5rem;
  margin:0 0 0 2%;
}
.header .search{
  width:10%;
  float: right;
  fill:#fff;
  height:.5rem;
  text-align: center;
  margin:0 2% 0 0;
}
.footer{
  background:rgb(67, 67, 67);
  width:100%;
  text-align: center;
  padding:.24rem 0 .4rem;
}
.footer p{
  color:#fff;
  font-size: .15rem;
  margin:.2rem 0 .1rem;
}
.footer span{
  color:rgb(204, 204, 204);
}
.toolbar{
  width:100%; 
  max-width:768px; 
  text-align: center;
  background: #fff;
}
.toolbar ul{
  overflow:hidden;
  border-top:1px solid #c81118;
  padding:.05rem 0;
}
.toolbar ul a{
  width:25%;
  display: block;
  float: left;
}
.toolbar ul img{
  width:.22rem;
  height:.22rem;
}
.toolbar ul p{
  color:#9c9c9c;
}
.page{
    width:100%; margin:.3rem auto .2rem; text-align:center; overflow:hidden;
}
.page a{
    background:#eee; width:.8rem; border-radius:.03rem; display:inline-block; margin:0 .15rem; height:.33rem; line-height:.33rem; float:left;
}
.page .select{
  display:inline-block; position:relative;width:.96rem; height:.3rem; float:left;
}
.page .select .val{
     border:1px solid #ccc; position:absolute; background:#eee; width:.96rem; height:.3rem; text-align:center; line-height:.3rem; border-radius:.04rem;
}
.page .select select{
    opacity:0; position:absolute; width:100%; left:0; height:.3rem;
}


/* 路由切换动画 */
.child-view {
  width: 100%;
  -webkit-transition: all 0.5s cubic-bezier(.55, 0, .1, 1);
  -webkit-transition:all .5s cubic-bezier(.55, 0, .1, 1);
  transition: all .5s cubic-bezier(.55, 0, .1, 1);
}
.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active {
  will-change: transform;
  -webkit-transition:all 0.5s;
  transition: all 0.5s;
  height: 100%;
  position: absolute;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-perspective: 1000;
          perspective: 1000;
}
.slide-right-enter {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.slide-left-enter {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.slide-left-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}



